import React from 'react';

import s from './Card.module.css';

//var props = {max : "max", min : "min", name : "name", img : "img", onClose : "onClose"}

export default function Card(props) {
  // acá va tu código
  var imgNumber = props.img;
  var url = `http://openweathermap.org/img/wn/${imgNumber}@2x.png`;

  // {`${e.cardContainer}`}

  return (
    <div className={s.cardContainer}> 
        <h3 className={s.cityName}>City: {props.name}</h3>
        <div>
            <p className={s.tempMax}>Max: {props.max}</p>
            <p className={s.tempMin}>Min: {props.min}</p>
            <img src={url} alt= {imgNumber} />
        </div>
        <button onClick={props.onClose}>Correr onClose()</button>
        <p>Card Component</p>
        

    </div>
    )
};