import React from 'react';

//Importo estilos
import s from "./SearchBar.modules.css"

export default function SearchBar(props) {
  // acá va tu código
  //Esto se haría con deconstructing?

  console.log()

  function submit(event) {
      event.preventDefault();
      props.onSearch("Londres");
  }

  return <div>Search Bar Component
    <div>
        <form onSubmit={submit} className={s.form}>
            <input className={s.input} placeholder="Ingrese ciudad a buscar.">
            </input>
            <button id={s.button} className={s.btn}>Agregar</button>
        </form>   
    </div>
  </div>
};