import React from 'react';

//Agrego esta línea, es necesario (confirmado en clase)
import Card from './Card.jsx';

//Agrego línea de estilos como parte del HW 07
import e from "./Cards.module.css"



export default function Cards(props) {
  // acá va tu código
  // tip, podés usar un map
    return (
        <div className={e.CardsContainer}>
            { // es buena practica preguntar si el elemento sobre el que iterar existe, antes de hacer la iteración per se.
                props.cities && props.cities.map(city => ( //////// Por que props.cities y no props!? Creo que por el destructuring.
                    <Card name={city.name} max={city.main.temp_max} min={city.main.temp_min} img={city.weather[0].icon} onClose={() => alert(city.name)} key={city.id}></Card>
                    ))
                }    
        
        </div>
        )
};